import {Box, Button, Checkbox, Container, FormControlLabel, Grid, Link, TextField, Typography} from "@mui/material";
import styles from './main.module.css';
import {useState} from "react";
import {init} from "../../api/init";
import ReactCodeInput from 'react-code-input';
import InputMask from 'react-input-mask';
import {loginByEmail, loginByPhone} from "../../api/login";
import {deleteAccount} from "../../api/delete";

export const Main = () => {
    const [emailStatus, setEmailStatus] = useState(true);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [phone, setPhone] = useState('');
    const [code, setCode] = useState('');
    const [state, setState] = useState(4);
    const [remained, setRemained] = useState(0);
    const [userData, setUserData] = useState({});

    const sendCode = async () => {
        if (emailStatus) {
            const result = await loginByEmail({ email, password });

            const { id, token } = result.data;

            setUserData({ id, token });

            setState(2);

            return;
        }

        const result = await init({
            verifyType: 2,
            phone: phone.replace(/[^0-9]/g, ''),
        });

        console.log(result);
        setState(1);
    }

    const loginPhone = async () => {
        const result = await loginByPhone({ phone: phone.replace(/[^0-9]/g, ''), code });

        const { id, token } = result.data;

        setUserData({ id, token });

        setState(2);
    }

    const delAccount = async () => {
        const { id, token } = userData;

        const result = await deleteAccount({ id, token });

        setState(3);
    }

    return (<Container component="main" maxWidth="xs" className={styles.main}>
        {state == 0 && <Box
            sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            className={styles.main__box}
        >
            <Typography component="h3" variant="h3">
                Welcome!
            </Typography>
            <Typography component="p">
                Sign to continue.
            </Typography>

            <div className={styles.main__select}>
                <div
                    onClick={() => setEmailStatus(false)}
                    className={emailStatus === false ? styles.main__select_item + ' ' + styles.active : styles.main__select_item}
                >Mobile</div>
                <div
                    onClick={() => setEmailStatus(true)}
                    className={emailStatus === true ? styles.main__select_item + ' ' + styles.active : styles.main__select_item}
                >Email</div>
            </div>

            <Box component="form" noValidate sx={{ mt: 1 }}>
                {emailStatus === true && <>
                <TextField
                    margin="normal"
                    fullWidth
                    id="email"
                    label="Email"
                    name="email"
                    autoComplete="email"
                    autoFocus
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <TextField
                    margin="normal"
                    fullWidth
                    name="password"
                    label="Password"
                    type="password"
                    id="password"
                    autoComplete="current-password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                </>}
                {emailStatus === false &&
                    <InputMask
                        mask="+7 ( 999 ) - 999 - 99 - 99"
                        value={phone}
                        onChange={(e) => setPhone(e.target.value)}
                        disabled={false}
                        maskChar=" "
                    >
                        {() => <TextField
                            margin="normal"
                            fullWidth
                            label="Phone number"
                            name="phone"
                            autoComplete="phone"
                            autoFocus
                        />}
                    </InputMask>

                }
                <Button
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    onClick={() => sendCode()}
                >
                    Sign In
                </Button>
            </Box>
        </Box>}
        {state == 1 && <Box
            sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            className={styles.main__box}
        >
            <Typography component="h3" variant="h3">
                Verification
            </Typography>
            <Typography component="p">
                This code was sent to {emailStatus ? `email ${email}` : `phone +${phone}`}
            </Typography>

            <Box component="form" noValidate sx={{ mt: 1 }} className={styles.main__code}>
                <ReactCodeInput
                    type='number'
                    fields={4}
                    value={code}
                    onChange={(e) => setCode(e)}
                />

                <Typography component="p">
                    Send code again:
                    <div>
                        <img src="/clock.svg" />
                        00:
                    </div>
                </Typography>
                <Button
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    disabled={code.length !== 4}
                    onClick={() => loginPhone()}
                >
                    Done
                </Button>
            </Box>
        </Box>}
        {state == 2 && <Box
            sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            className={styles.main__box}
        >
            <Typography component="h3" variant="h3">
                Delete account
            </Typography>
            <Typography component="p">
                When deleted, the account cannot be restored. Are you sure you want to delete your account?
            </Typography>

            <Box component="form" noValidate sx={{ mt: 1 }} className={styles.main__code}>
                <Button
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    onClick={() => delAccount()}
                >
                    Yes
                </Button>

                <Button
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    onClick={() => setState(0)}
                >
                    No
                </Button>
            </Box>
        </Box>}
        {state == 3 && <Box
            sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            className={styles.main__box}
        >
            <img src="/del.png" />

            <Typography component="h3" variant="h3">
                Account successfully deleted!
            </Typography>

            <Box component="form" noValidate sx={{ mt: 1 }} className={styles.main__code}>
                <Button
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    disabled={code.length !== 4}
                    onClick={() => setState(0)}
                >
                    Continue
                </Button>
            </Box>
        </Box>}
        {state == 4 && <Box
            sx={{
                marginTop: 8,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            className={styles.main__box}
        >
            <Typography component="h3" variant="h3">
                How to delete account?
            </Typography>

            <Typography component="p" variant="p">
                Why did you decide to delete the account?
            </Typography>

            <div
                className={styles.main__link}
                onClick={() => setState(5)}
            >
                <span>How do I delete my account in the app?</span>

                <img src="/arrow.svg" />
            </div>

            <div
                className={styles.main__link}
                onClick={() => setState(5)}
            >
                <span>How do I delete my account on the website?</span>

                <img src="/arrow.svg" />
            </div>

            <Box component="form" noValidate sx={{ mt: 1 }} className={styles.main__code}>
                <Button
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                    onClick={() => setState(0)}
                >
                    Delete account
                    <img src="/parrow.svg" />
                </Button>
            </Box>
        </Box>}
    </Container>)
}