import axios from "axios";

export const loginByEmail = ({ email, password, code }) => {
    return axios.post('/api/v1.0/users/login_by_email', {
        email,
        plainPassword: password,
    })
}

export const loginByPhone = ({ phone, code }) => {
    return axios.post('/api/v1.0/users/login_by_phone', {
        phone,
        verificationCode: code,
    })
}